<!--  -->
<template>
    <div id='headTop'>
        <img src="../../../static/image/l.png" alt="" class='header-icon'  v-if='goBack' @click='$router.go(-1)'>
        <p class='header-title' v-if='headTitle' >{{headTitle}}</p>
        <p></p>
    </div>
</template>

<script>
//这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
//例如：import 《组件名称》 from '《组件路径》';

export default {
//import引入的组件需要注入到对象中才能使用
components: {},
data() {
//这里存放数据
return {

};
},
//监听属性 类似于data概念
computed: {
    headTitle(){
        return this.$store.getters.headTitles;
    },
    goBack(){
        return this.$store.getters.goBack;
    }
},
//监控data中的数据变化
watch: {},
//方法集合
methods: {

},
//生命周期 - 创建完成（可以访问当前this实例）
created() {

},
//生命周期 - 挂载完成（可以访问DOM元素）
mounted() {

},
beforeCreate() {}, //生命周期 - 创建之前
beforeMount() {}, //生命周期 - 挂载之前
beforeUpdate() {}, //生命周期 - 更新之前
updated() {}, //生命周期 - 更新之后
beforeDestroy() {}, //生命周期 - 销毁之前
destroyed() {}, //生命周期 - 销毁完成
activated() {}, //如果页面有keep-alive缓存功能，这个函数会触发
}
</script>
<style lang='scss' scoped>
//@import url(); 引入公共css类
#headTop{
    width:100%;
    padding:0 3%;
    height:90px;
    display:flex;
    justify-content:center;
    align-items:center;
    background:#fff;
    position:fixed;
    top:0;
    z-index:1;
}
.header-icon{
    width:32px;
    height:32px;
    display:block;
}
.header-title{
    font-size:32px;
    color:#000;
    width:94%;
    line-height:90px;
}
</style>


