<!--  -->
<template>
    <div id='footerBtm' v-if='showBtm' >
        <div class='footer-list'  v-for="(item,index) in tabList3"  :key='index'  @click="navTab(index)">
            <router-link :to='item.href'>
                <img  :src='[$route.path==item.href?item.activeIcon:item.icon]' alt="" class='footer-icon' >
                <span class='footer-texts' v-if='$route.path==item.href' >{{item.tabTitle}}</span>
                <span class='footer-text' v-else>{{item.tabTitle}}</span>
            </router-link>
        </div>
    </div>
</template>

<script>
    //这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
    //例如：import 《组件名称》 from '《组件路径》';

    export default {
    //import引入的组件需要注入到对象中才能使用
    data() {
        return {
            tabList3:[
                {
                'tabTitle':'首页',
                'curr':true,
                'icon':'../../../static/image/tab-icon1.png',
                'activeIcon':'../../../static/image/tab-icon1-1.png',
                'href':'/home'
                },
                {
                'tabTitle':'分类',
                'curr':false,
                'icon':'../../../static/image/tab-icon2.png',
                'activeIcon':'../../../static/image/tab-icon2-2.png',
                'href':'/classify'
                },
                {
                'tabTitle':'购物车',
                'curr':false,
                'num':2,
                'icon':'../../../static/image/tab-icon4.png',
                'activeIcon':'../../../static/image/tab-icon4-4.png',
                'href':'/car'
                },
                {
                'tabTitle':'我的',
                'curr':false,
                'icon':'../../../static/image/tab-icon5.png',
                'activeIcon':'../../../static/image/tab-icon5-5.png',
                'href':'/main'
                }
            ]
        };
    },
    //监听属性 类似于data概念
    computed: {
        showBtm(){
            return this.$store.getters.showBtm
        }
    },
    //监控data中的数据变化
    watch: {},
    //方法集合
    methods: {
        // 底部导航点击下标
        navTab:function(value){
        }
    },
    //生命周期 - 创建完成（可以访问当前this实例）
    created() {

    },
    //生命周期 - 挂载完成（可以访问DOM元素）
    mounted() {

    },
    beforeCreate() {}, //生命周期 - 创建之前
    beforeMount() {}, //生命周期 - 挂载之前
    beforeUpdate() {}, //生命周期 - 更新之前
    updated() {}, //生命周期 - 更新之后
    beforeDestroy() {}, //生命周期 - 销毁之前
    destroyed() {}, //生命周期 - 销毁完成
    activated() {}, //如果页面有keep-alive缓存功能，这个函数会触发
    }
</script>
<style lang='scss'>
    #footerBtm{
        width:100%;
        height:100px;
        display: flex;
        justify-content:space-around;
        position:fixed;
        left:0;
        bottom:0;
        border-top:1px solid #b4b4b4;
        background:#fff;
    }
    .footer-list{
        flex-grow:1;
        display:flex;
        justify-content: center;
        flex-direction:column;

    }
    .footer-icon{
        width:50px;
        height:50px;
        margin:0 auto;
    }
    .footer-text{
        font-size:12px;
        color:#b4b4b4;
        display:block;
        text-align: center;
    }
    .footer-texts{
        font-size:12px;
        color:#f00;
        display:block;
        text-align: center;
    }
</style>


