import Vue from 'vue';
import Vuex from 'vuex';
import Toast from '@nutui/nutui/dist/packages/toast/toast.js';
import Dialog from '@nutui/nutui/dist/packages/dialog/dialog.js';

Vue.use(Vuex);

const store=new Vuex.Store({
    // 设置全局要访问的state值
    state:{
        headTitle:'陕旅优品',    //头部标题
        goBack:false,   //返回箭头
        showBtm:true,  //底部导航
        car:localStorage["car"]?JSON.parse(localStorage["car"]): [] ,  //购物车,
        all_money:0, //总价
        all_selsect:true, //全选
        search:localStorage["search"]?JSON.parse(localStorage["search"]): [] //搜索历史记录
    },
    // 实时监听state值的变化
    getters:{
        headTitles(state){
            return state.headTitle
        },
        goBack(state){
            return state.goBack
        },
        showBtm(state){
            return state.showBtm
        },
        carList(state){
            // 初始化全选状态
            if(state.all_selsect){
                state.car.forEach((item)=>{
                    item.selected=true
                }) 
            }
            localStorage.setItem("search",JSON.stringify(state.car)); 
            return state.car
        },
        allMoney(state){
            let all_money=0
            state.car.forEach((item)=>{
                if(item.selected){
                    all_money+=item.num*item.sellPrice
                }
            })
            return state.all_money=all_money
        },
        allSelsect(state){
            return state.all_selsect
        },
        searchList(state){
            localStorage.setItem("search",JSON.stringify(state.search)); 
            return state.search
        }
    },
    // 改变state里的初始值 同步的
    mutations :{
        HEADTITLE(state,title){
            return state.headTitle=title
        },
        GOBACK(state,back){
            return state.goBack=back
        },
        SHOWBTM(state,show){
            return state.showBtm=show
        },
        ADDGoods(state,data){
            let datas={
                title:data.title,
                img:data.pictures[0],
                num:1,
                sellPrice:data.sellPrice,
                marketPrice:data.marketPrice,
                selected:true,
                productId:data.productId
            }
            let index=-1
            index=state.car.findIndex(item=>{
                return item.productId==data.productId
             })
             if(index==-1){
                state.car.push(datas)
             }else{
                state.car[index].num++
             }
             Toast.success('添加成功！');
        },
        ADDNUM(state,productId){
            let index=state.car.findIndex(item=>{
                return item.productId==productId
             })
            return state.car[index].num++
        },
        JIANNUM(state,productId){
            let index=state.car.findIndex(item=>{
                return item.productId==productId
             })
            if(state.car[index].num<=1){
               return state.car[index].num=1
            }else{
               return state.car[index].num--
            }
        },
        SELECT(state,productId){
            let index=state.car.findIndex(item=>{
                return item.productId==productId
            })
            state.car[index].selected=!state.car[index].selected
            let flag=state.car.some((item)=>{
                return item.selected==false
            })
            if(!flag){
                state.all_selsect=true
            }else{
                state.all_selsect=false
            }
        },
        SELECTALL(state){
            state.all_selsect=!state.all_selsect
            if(state.all_selsect){
                state.car.forEach((item)=>{
                    item.selected=true
                })
            }else{
                state.car.forEach((item)=>{
                    item.selected=false
                })
            }
        },
        DEL(state){
            let flag=state.car.some((item)=>{
                return item.selected==true
            })
            if(!flag){
                Toast.text('请至少选择一个商品')
            }else{
                Dialog[0]({
                    title:'确定从购物车中删除吗？',
                    onOkBtn() {  //确定按钮点击事件
                        this.close(); //关闭对话框
                        var list=[];
                        state.car.forEach((item,index)=>{
                            if(!item.selected){
                                list.push(item)
                            }
                        })
                        return state.car=list
                    },
                })
            }
        },
        ADDSEARCH(state,text){
            if(state.search.indexOf(text)==-1){
                return state.search.push(text)
            }
        },
        DELSEARCH(state){
            return state.search=[]
        }
    },
    // 异步触发mutations里面的方法 在外部组件里进行全局执行actions里面方法的时候，你只需要用执行this.$store.dispatch('headTitle'，132) 这样就可以全局改变改变标题的值了
    actions:{
        headTitle({commit},title){
            commit('HEADTITLE',title)
        },
        goBack({commit},back){
            commit('GOBACK',back)
        },
        showBtm({commit},show){
            commit('SHOWBTM',show)
        },
        addGoods({commit},data){
            commit('ADDGoods',data)
        },
        addNum({commit},id){
            commit('ADDNUM',id)
        },
        jianNum({commit},id){
            commit('JIANNUM',id)
        },
        select({commit},id){
            commit('SELECT',id)
        },
        selectAll({commit}){
            commit('SELECTALL')
        },
        del({commit}){
            commit('DEL')
        },
        addSearch({commit},text){
            commit('ADDSEARCH',text)
        },
        delSearch({commit}){
            commit('DELSEARCH')
        }
    }
})

export default store;


