import Vue from "vue";
import VueRouter from "vue-router";
Vue.use(VueRouter)
// 按模块加载...
const home= () => import('../page/home/home')
const classify= () => import('../page/classify/classify')
const car= () => import('../page/car/car')
const shopDetails= () => import('../page/shopDetails/shopDetails')
const main= () => import('../page/main/main')
const shopList= () => import('../page/shopList/shopList')
const listMore= () => import('../page/listMore/listMore')
const search= () => import('../page/search/search')
const order= () => import('../page/order/order')


 const routes=[{
    path: '/',
    redirect: '/home',
    name:'home'
  },{
    path:'/home',
    component: home,
    name:'home'
  },{
    path:'/classify',
    component: classify,
    name:'classify'
  },{
    path:'/car',
    component: car,
    name:'car'
  },{
    path:'/shopDetails/:id',
    component: shopDetails,
    name:'shopDetails'
  },{
    path:'/main',
    component: main,
    name:'main'
  },{
    path:'/shopList/:id',
    component: shopList,
    name:'shopList'
  },{
    path:'/listMore/:id',
    component: listMore,
    name:'listMore'
  },{
    path:'/search',
    component: search,
    name:'search'
  },{
    path:'/order',
    component: order,
    name:'order'
  }
]

const router = new VueRouter({
	routes,
  // mode: 'history',
  //  vuex的严格模式
  // strict: process.env.NODE_ENV !== 'production',
  // 这个整体做的是：在路由的history模式下，一些列表页利用缓存模式来记录位置（一般是返回不刷新，前进刷新）,一般用了scrollBehavior，
  //同时还用keep-alive(缓存),activated(缓存下触发的钩子)配合做列表页的返回记录位置。缓存模式也有坑，就是何时清除缓存，一般是从新进入页面就清除。
  //回到主题，滚动行为就是：例如一个列表页，滑动了很多，点进去、再返回记录刚刚的位置
	scrollBehavior (to, from, savedPosition) {
	    if (savedPosition) {
		    return savedPosition
		} else {
			if (from.meta.keepAlive) {
				from.meta.savedPosition = document.body.scrollTop;
			}
		    return { x: 0, y: to.meta.savedPosition ||0}
		}
	}
})

export default router



